const DEFAULT_ALLOWED_UIDS = [
  '217122260',  // ‰Ω†ÁöÑ UID
  '246490729',
  // ‚Ä¶ÂèØ‰ª•Âú®ËøôÈáåÁªßÁª≠Ë°•ÂÖÖÈªòËÆ§ÁöÑ UID
];

/**
 * Ëé∑ÂèñÂΩìÂâçÁôΩÂêçÂçïÔºà‰ºòÂÖàËØª localStorageÔºåÊ≤°ÊúâÂàôËøîÂõû DEFAULT_ALLOWED_UIDSÔºâ
 * @returns {string[]}
 */
export function getAllowedUids() {
  try {
    const raw = localStorage.getItem('allowed_uids');
    const arr = JSON.parse(raw);
    if (Array.isArray(arr)) return arr;
  } catch(e) { /* ignore */ }
  return DEFAULT_ALLOWED_UIDS;
}

/**
 * Ë¶ÜÁõñÂÜôÂÖ•ÁôΩÂêçÂçï
 * @param {string[]} list
 */
export function setAllowedUids(list) {
  localStorage.setItem('allowed_uids', JSON.stringify(list));
}
