// src/stores/user.js
import { defineStore } from 'pinia';

export const useUserStore = defineStore('user', {
  // 1. stateÔºöÂàùÂßãÂåñÊó∂Ôºå‰ªé localStorage ËØªÔºåÁ°Æ‰øùÂà∑Êñ∞ÂêéËøòËÉΩÊãøÂà∞‰∏äÊ¨°ÁôªÂΩïÁä∂ÊÄÅ
  state: () => ({
    currentUser: localStorage.getItem('cm_currentUser') || null,
    avatarMap: JSON.parse(localStorage.getItem('cm_avatarMap') || '{}')
  }),

  // 2. gettersÔºöÂèØÈÄâÔºåÁªôÊ®°ÊùøÊàñÂà´ÁöÑ Store Áî®Êù•Âø´ÈÄüËØªÂÄº
  getters: {
    isLoggedIn: state => !!state.currentUser,

    // Êñ∞Â¢ûÔºöÁªü‰∏ÄÁöÑÂ§¥ÂÉèÂèñÂÄº + ÈªòËÆ§ÂõûÈÄÄ
    getAvatar: (state) => {
      return (uid) => {
        const url = state.avatarMap[uid];
        return url && url.length > 0
          ? url
          : 'https://placehold.co/60';
      };
    }
  },

  // 3. actionsÔºöÊâÄÊúâ‰øÆÊîπ state ÁöÑÂú∫ÊôØÔºåÈÉΩÈ°∫Â∏¶ÂêåÊ≠•Âà∞ localStorage
  actions: {
    login(uid) {
      console.log('üî• userStore.login called with', uid)
      this.currentUser = uid
      localStorage.setItem('cm_currentUser', uid)
    },
    logout() {
      this.currentUser = null
      localStorage.removeItem('cm_currentUser')
    },
    setAvatar(uid, url) {
      // Êõ¥Êñ∞ÂÜÖÂ≠ò‰∏≠ avatarMap
      this.avatarMap = { ...this.avatarMap, [uid]: url }
      // ÂêåÊ≠•Âà∞ localStorage
      localStorage.setItem('cm_avatarMap', JSON.stringify(this.avatarMap))
    }
  }
})
